<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <v-btn color="primary">Success</v-btn>

  </div>
</template>

<script>
export default {
  name: 'About',
  data () {
    return {
      msg: 'Sobre mi'
    }
  }
}
</script>

